import { CellType } from "./types/CellType";
import { Point } from "./types/Point";

export class Help {
  public cols: number = 5;
  public rows: number = 5;
  public grid: HTMLElement;
  public map: number[][];
  public queue: Point[] = [];

  constructor() {
    this.grid = document.getElementById('help') as HTMLElement;
    this.map = (new Array(this.rows))
      .fill([])
      .map(() => (new Array(this.cols)).fill(0));

    this.createDom();
  }

  private createDom() {
    for (let i = 0; i < this.rows; i++) {
      const row = document.createElement('div');
      row.classList.add('row');

      for (let j = 0; j < this.cols; j++) {
        const cell = document.createElement('div');
        cell.classList.value = 'cell type-0';
        cell.dataset.x = j.toString();
        cell.dataset.y = i.toString();
        row.appendChild(cell);
      }

      this.grid.appendChild(row);
    }
  }

  public update() {
    // remove duplicate coordinates
    this.queue = this.queue
      .reverse()
      .filter((point, index, self) => {
        return index === self.findIndex((t) => {
          return t.x == point.x && t.y == point.y;
        });
      });

    // draw points on grid
    while(this.queue.length) {
      const point = this.queue.shift();
      if (!point) break;

      const selector = `#help [data-x="${point.x}"][data-y="${point.y}"]`;
      const cell = document.querySelector(selector) as HTMLElement;
      if (!point) break;

      cell.classList.value = 'cell';
      cell.classList.add(`type-${point.type}`);
    }
  }

  public addQueue(point: Point) {
    this.map[point.y][point.x] = point.type;
    this.queue.push(point);
  }
}
